const Servers = () => (
  <Layout>
    <SEO title="Servers" />
    <h1>サーバー一覧</h1>
    <h2>仕組み</h2>
    <p>ぽっけドールは複数のサーバーが存在します。対応するコマンドまたは看板をクリックすることで接続することが可能です</p>
    <h2>おおっと。サーバーが落ちているぞ？</h2>
    <p>
      ぽっけドールでは、サーバーの維持費を軽減するため、各サーバーの人数が0人の状態が一時間以上続いた場合に自動でシャットダウンされます。
      コマンドで接続する場合、サーバーが落ちていたら自動で起動させます。この時プレイヤーが参加できるようになるまでに数分かかる場合があります。
    </p>
    <h3>Lobby</h3>
    <p>version: 1.12.x ~ 1.15.x</p>
    <p>プレイヤーが最初に接続されるサーバーです。</p>
    <h3>DollZ</h3>
    <h4>DollZⅢ</h4>
    <h3>Home</h3>

    <Link to="/">Go back to the homepage</Link>
  </Layout>
)

export default Servers